// <Section>
.problems {
  padding: 25px 0 50px 0;

  background-image: linear-gradient(
    to bottom,
    $text-second-color 72%,
    $white-color 72%,
    $white-color 100%
  );

  @media screen and (min-width: $tablet-768) {
    padding-top: 60px;

    background-image: linear-gradient(
      to bottom,
      $text-second-color 66%,
      $white-color 66%,
      $white-color 100%
    );
  }

  @media screen and (min-width: $desktop-1366) {
    padding: 100px 50px 130px;

    background-image: linear-gradient(
        to bottom,
        transparent 60%,
        $white-color 60%,
        $white-color 100%
      ),
      url(../images/compressed/problems/campfire-bg.svg),
      linear-gradient(to bottom, $text-second-color 60%, transparent 60%, transparent 100%);
    background-size: contain, 400px 445px, contain;
    background-repeat: no-repeat, no-repeat, no-repeat;
    background-position: 0 0, bottom 260px right, 0 0;
  }
}
// <H2>
.problems-title {
  margin-bottom: 25px;
  padding-right: 40px;

  font-weight: 800;
  font-size: 20px;
  line-height: 1.36;
  letter-spacing: 0.03em;
  text-align: start;

  color: $white-color;
  // 1366+
  @media screen and (min-width: $desktop-1366) {
    margin-bottom: 70px;
    font-size: 46px;
    line-height: 1.3;
  }
}
// <Span>
.accent {
  color: $accent-color;
}
// <UL>
.problems-list {
  padding: 20px 30px;

  background-color: $white-color;
  box-shadow: -5px 8px 26px $shadow-primary-color;

  @media screen and (min-width: $tablet-768) {
    padding: 30px 45px 10px 95px;
  }
  @media screen and (min-width: $desktop-1366) {
    padding: 70px 110px 60px;
  }
}
// <LI>
.problems-item {
  counter-increment: section;

  font-weight: 700;
  font-size: 14px;
  line-height: 1.714;
  letter-spacing: 0.08em;
  // max 767
  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  // min 768+
  @media screen and (min-width: $tablet-768) {
    display: inline-flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: flex-start;

    width: calc((100% - 2 * 30px) / 2);
  }
  // 768 - 1365
  @media screen and (min-width: $tablet-768) and (max-width: 1365px) {
    &:nth-child(odd) {
      margin-right: 20px;
    }
    &:not(:nth-last-child(-n + 2)) {
      margin-bottom: 20px;
    }
  }
  // 1366+
  @media screen and (min-width: $desktop-1366) {
    width: calc((100% - 3 * 30px) / 3);

    font-size: 20px;
    line-height: 1.4;
    // all 3th [ 3, 6, 9, .... ]
    &:nth-child(3n + 3) {
      margin-right: 0;
    }
    // all not 3th [ 1, 2, 4, 5, .... ]
    &:not(:nth-child(3n + 3)) {
      margin-right: 40px;
    }
    // all not 3th last items
    &:not(:nth-last-child(-n + 3)) {
      margin-bottom: 30px;
    }
    // all 3th last items
    &:nth-last-child(-n + 3) {
      margin-bottom: 0;
    }
  }
}

// Counter
.problems-item::before {
  content: '\2015  0'counter(section) '';
  display: block;

  margin-bottom: 10px;

  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: 0.08em;

  color: $accent-color;
}
