// Всі перемінні. Кольори, тіні, фонти, кубік, час і т.д. [ $ім'я ]

// Colors
// Named colors
$white-color: #ffffff;
$black-color: #000000;
// colors Text
$text-primary-color: #b9b9b9;
$text-second-color: #111518;
$text-third-color: #3d3d3d;
// color Accent
$accent-color: #ff6b09;
// color backgrounds
$bg-main-color: rgba(17, 21, 24, 0.9);
$bg-second-color: #f1f1f1;
// color Shadows
$shadow-primary-color: rgba(0, 0, 0, 0.04);
$shadow-second-color: rgba(0, 0, 0, 0.1);
$shadow-third-color: rgba(0, 0, 0, 0.25);

// Times for transformations
$time0: 0ms;
$time250: 250ms;
$time500: 500ms;
$time750: 750ms;
$time1000: 1000ms;
$time1250: 1250ms;
$time1500: 1500ms;
$time1750: 1750ms;
$time2000: 2000ms;
$time2250: 2250ms;
$time2500: 2500ms;
// Cubic
$cubic: cubic-bezier(0.4, 0, 0.2, 1);

// add custom animations to HTML  [ https://animate.style/ ]
// add animation class      [ class="animate__animated" ]
// add prefix of animations [ class="animate__" ]
// add name of animation after prefix [ class="__name-animation" ]
